.main-slider {
    position: relative;
    margin-bottom: 30px;
    background: url(../images/intro/slider/banner/07.png) no-repeat top;
    &::ayter {
        content: "";
    }
}
.hover-target {
    font-weight: 500;
}
.intro {
    .header {
        position: absolute;
    }
}
.slider-top-banner {
    
    .slider-3 {
        height: 100%;
        width: 100%;
        background: url(../images/intro/slider/banner/06.png) no-repeat top;
        position: absolute;
        left: -15%;
        top: 5%;
        z-index: 9;
    }
    .slider-banner {
        position: relative;
        z-index: 9;
        
        .slider-main {
            margin-top: 50px;
        }
        .slider-1 {
            height: 100%;
            width: 100%;
            background: url(../images/intro/slider/banner/04.png) no-repeat top;
            background-size: 10%;
            position: absolute;
            top: -7px;
            left: -44%;
            z-index: 99;
        }
        .slider-2 {
            height: 100%;
            width: 100%;
            background: url(../images/intro/slider/banner/03.png) no-repeat top;
            background-size: 85%;
            position: absolute;
            top: 20px;
            z-index: 9;
        }
        .slider-4 {
            height: 100%;
            width: 100%;
            background: url(../images/intro/slider/banner/02.png) no-repeat top;
            background-size: 50%;
            position: absolute;
            right: -35%;
            top: 18%;
            z-index: 9;
        }
    }
}

.slider-text {
    padding: 100px 0 0;
    h1 {
        font-size: 50px;
        line-height: 65px;
        margin: 0 auto;
        letter-spacing: 2px !important;
        b {
            color: $primary !important;
        }
    }
    img {
        width: 130px;
    }
    p.slider-sub-text {
        font-size: 20px;
        font-weight: 500;
        letter-spacing: 6px;
    }
    .templt {
        text-decoration: line-through;
    }
}

// links
.slider-copyright {
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    left: -45px;
    color: $body-text;
    z-index: 9;
    a {
        color: $body-text;
        font-weight: 700;
        letter-spacing: 1px;
        transform: rotate(-90deg);
        display: block;
    }
}

.slider-breed {
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    left: auto;
    right: -60px;
    z-index: 9;
    color: $body-text;
    a {
        color: $body-text;
        font-weight: 700;
        letter-spacing: 1px;
        transform: rotate(-90deg);
        display: block;
    }
}


/*-------*/

.slider-area {
    width: 100%;
    .area-circles {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        li {
            position: absolute;
            display: block;
            list-style: none;
            width: 20px;
            height: 20px;
            background: rgba(255, 255, 255, 0.6);
            animation: circles-animate 25s linear infinite;
            bottom: 100px;
            &:nth-child(1) {
                left: 10%;
                width: 80px;
                height: 80px;
                animation-delay: 0s;
            }
            &:nth-child(2) {
                left: 5%;
                width: 20px;
                height: 20px;
                animation-delay: 2s;
                animation-duration: 12s;
            }
            &:nth-child(3) {
                left: 70%;
                width: 20px;
                height: 20px;
                animation-delay: 4s;
            }
            &:nth-child(4) {
                left: 15%;
                width: 60px;
                height: 60px;
                animation-delay: 0s;
                animation-duration: 18s;
            }
            &:nth-child(5) {
                left: 65%;
                width: 20px;
                height: 20px;
                animation-delay: 0s;
            }
            &:nth-child(6) {
                left: 75%;
                width: 110px;
                height: 110px;
                animation-delay: 3s;
            }
            &:nth-child(7) {
                left: 20%;
                width: 150px;
                height: 150px;
                animation-delay: 7s;
            }
            &:nth-child(8) {
                left: 50%;
                width: 25px;
                height: 25px;
                animation-delay: 15s;
                animation-duration: 45s;
            }
            &:nth-child(9) {
                left: 20%;
                width: 15px;
                height: 15px;
                animation-delay: 2s;
                animation-duration: 35s;
            }
            &:nth-child(10) {
                left: 85%;
                width: 150px;
                height: 150px;
                animation-delay: 0s;
                animation-duration: 11s;
            }
        }
    }
}

@keyframes circles-animate {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }
    100% {
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
}

@media (max-width: 1300px) {
    .slider-top-banner {
        .slider-banner {
            .slider-3 {
                position: absolute;
                left: -13%;
                top: 24%;
                z-index: 9;
            }
        }
    }
    .slider-area {
        .area-circles {
            li {
                &:first-child {
                    width: 40px;
                    height: 40px;
                }
                &:nth-child(6) {
                    width: 60px;
                    height: 60px;
                }
                &:nth-child(7) {
                    width: 60px;
                    height: 60px;
                }
                &:nth-child(10) {
                    width: 60px;
                    height: 60px;
                }
            }
        }
    }
}

@media (max-width: 1199px) {
    .slider-top-banner {
        .slider-banner {
            .slider-3 {
                position: absolute;
                left: -7%;
                top: 13%;
                z-index: 9;
            }
        }
    }
}

@media (min-width: 1024px) {
    .slider-top-banner {
        .slider-banner {
            .slider-4 {
                background-size: 64%;
                position: absolute;
                right: -29%;
                top: 40%;
                z-index: 9;
            }
            .slider-3 {
                display: none;
            }
        }
    }
}
@media (max-width: 991px) {
    .slider-text h1 {
        font-size: 40px;
        line-height: 55px;
    }
    .slider-top-banner {
        .slider-banner {
            .slider-1 {
                top: 10px;
            }
            .slider-4 {
                top: 45%;
            }
        }
    }
}
@media (max-width: 768px) {
    .slider-top-banner {
        .slider-banner {
            .slider-3 {
                display: none;
            }
            .slider-4 {
                right: -26%;
            }
        }
    }
}

@media (max-width: 575px) {
    .slider-top-banner {
        margin-bottom: 0px;

        .slider-3 {
            background-size: 40%;
        }
        .slider-banner {
            .slider-main {
                margin-top: 30px;
            }
            .slider-1 {
                background-size: 12%;
            }
            .slider-2 {
                background-size: 72%;
                top: 5px;
            }
        }
    }
    .hover-target {
        margin-bottom: 15px;
    }
}
@media (max-width: 360px) {
    .slider-text {
        h1 {
            font-size: 34px;
            line-height: 47px;
        }
    }
}
.iq-fadebounce {
    position: absolute;
    -webkit-animation-name: fadebounce;
    -moz-animation-name: fadebounce;
    -ms-animation-name: fadebounce;
    -o-animation-name: fadebounce;
    animation-name: fadebounce;
    -webkit-animation-duration: 3s;
    -moz-animation-duration: 3s;
    -ms-animation-duration: 3s;
    -o-animation-duration: 3s;
    animation-duration: 3s;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    -ms-animation-iteration-count: infinite;
    -o-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}

@-moz-keyframes fadebounce {
    0% {
        -moz-transform: translateY(0);
        transform: translateY(0);
        opacity: 1
    }
    50% {
        -moz-transform: translateY(20px);
        transform: translateY(20px);
        opacity: 1
    }
    100% {
        -moz-transform: translateY(0);
        transform: translateY(0);
        opacity: 1
    }
}

@-webkit-keyframes fadebounce {
    0% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
        opacity: 1
    }
    50% {
        -webkit-transform: translateY(20px);
        transform: translateY(20px);
        opacity: 1
    }
    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
        opacity: 1
    }
}

@-o-keyframes fadebounce {
    0% {
        -o-transform: translateY(0);
        transform: translateY(0);
        opacity: 1
    }
    50% {
        -o-transform: translateY(20px);
        transform: translateY(20px);
        opacity: 1
    }
    100% {
        -o-transform: translateY(0);
        transform: translateY(0);
        opacity: 1
    }
}

@-ms-keyframes fadebounce {
    0% {
        -ms-transform: translateY(0);
        transform: translateY(0);
        opacity: 1
    }
    50% {
        -ms-transform: translateY(20px);
        transform: translateY(20px);
        opacity: 1
    }
    100% {
        -ms-transform: translateY(0);
        transform: translateY(0);
        opacity: 1
    }
}

@keyframes fadebounce {
    0% {
        transform: translateY(0);
        opacity: 1
    }
    50% {
        transform: translateY(20px);
        opacity: 1
    }
    100% {
        transform: translateY(0);
        opacity: 1
    }
}

.iq_shake-horizontal {
    -webkit-animation: shake-horizontal 30s cubic-bezier(0.455, 0.030, 0.515, 0.955) infinite both;
    animation: shake-horizontal 30s cubic-bezier(0.455, 0.030, 0.515, 0.955) infinite both;
}

@-webkit-keyframes shake-horizontal {
    0%,
    100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    10%,
    30%,
    50%,
    70% {
        -webkit-transform: translateX(-10px);
        transform: translateX(-10px);
    }
    20%,
    40%,
    60% {
        -webkit-transform: translateX(10px);
        transform: translateX(10px);
    }
    80% {
        -webkit-transform: translateX(8px);
        transform: translateX(8px);
    }
    90% {
        -webkit-transform: translateX(-8px);
        transform: translateX(-8px);
    }
}

@keyframes shake-horizontal {
    0%,
    100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    10%,
    30%,
    50%,
    70% {
        -webkit-transform: translateX(-10px);
        transform: translateX(-10px);
    }
    20%,
    40%,
    60% {
        -webkit-transform: translateX(10px);
        transform: translateX(10px);
    }
    80% {
        -webkit-transform: translateX(8px);
        transform: translateX(8px);
    }
    90% {
        -webkit-transform: translateX(-8px);
        transform: translateX(-8px);
    }
}